import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  //    component: () => import(/* webpackChunkName: "about" */ '../views/... .vue')
];

const router = new VueRouter({
  mode: "history",
	strict: process.env.NODE_ENV !== 'production',
  routes
});

export default router;
